@import "https://fonts.googleapis.com/css2?family=DM+Sans:wght@700&family=Titan+One&display=swap";
body {
  font-family: var(--primary-font);
  letter-spacing: .04em;
  color: var(--primary-text-color);
}

h1, h2, h3, ul, p {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

.container {
  padding: 0 20px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
    margin: 0 auto;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 34px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
    padding: 0 115px;
  }
}

:root {
  --primary-font: "DM Sans", sans-serif;
  --secondary-font: "Titan One", cursive, sans-serif;
  --primary-text-color: #fff;
  --secondary-text-color: #000;
  --accent-color: #d41443;
  --info-color: #ffa5ba;
  --description-color: #907e82;
}

.visually-hidden {
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.button {
  letter-spacing: .06em;
  color: var(--accent-color);
  background-color: var(--primary-text-color);
  cursor: pointer;
  border-style: none;
  border-radius: 22px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.31;
}

.button:hover, .button:focus {
  background-color: #ecebeb;
}

.button--red {
  background-color: var(--accent-color);
  color: var(--primary-text-color);
}

.button--red:hover, .button--red:focus {
  background-color: #c7133e;
}

.button--catalin {
  letter-spacing: .06em;
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: #d41443;
  border-style: none;
  border-radius: 30px;
  align-self: center;
  width: 35%;
  height: 40px;
  margin-top: 10px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.31;
}

.button--catalin:hover, .button--catalin:focus {
  color: #fff;
  background-color: #c7133f;
}

.title {
  font-family: var(--secondary-font);
  letter-spacing: .06em;
  text-transform: uppercase;
  color: var(--accent-color);
  font-size: 58px;
  line-height: 1.14;
}

.subtitle {
  color: var(--secondary-text-color);
  font-size: 16px;
  font-weight: 500;
  line-height: 1.86;
}

.header {
  background-color: #ffb8ca;
  width: 100%;
  position: absolute;
}

.header__container {
  z-index: 2;
  justify-content: space-between;
  align-items: center;
  padding-top: 6px;
  padding-bottom: 6px;
  display: flex;
  position: relative;
}

@media screen and (min-width: 768px) {
  .header__container {
    justify-content: start;
    padding-top: 37px;
    padding-bottom: 37px;
  }
}

.burger-btn {
  background-color: #0000;
  border: none;
  padding: 0;
}

.burger-btn__icon {
  width: 26px;
  height: 26px;
  position: absolute;
  top: 67px;
  left: 390px;
}

@media screen and (min-width: 768px) {
  .burger-btn {
    z-index: 5;
    margin-left: auto;
    margin-right: 30px;
    position: relative;
  }
}

@media screen and (min-width: 1280px) {
  .burger-btn {
    display: none;
  }
}

.modal-btn {
  cursor: pointer;
  color: var(--accent-color);
  background-color: #fff;
  border: none;
  border-radius: 22px;
  justify-content: center;
  align-items: center;
  min-width: 160px;
  height: 44px;
  padding: 0;
  font-family: inherit;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.3;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.modal-btn:hover, .modal-btn:focus {
  background-color: #ecebeb;
}

.modal-btn__icon {
  width: 6px;
  height: 10px;
  margin-left: 12px;
}

@media screen and (max-width: 767px) {
  .modal-btn {
    display: none;
  }
}

@media screen and (min-width: 1280px) {
  .modal-btn {
    margin-left: 141px;
  }
}

.nav {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.nav-list {
  display: flex;
}

@media screen and (max-width: 1279px) {
  .nav-list {
    display: none;
  }
}

.nav-list__item:not(:last-child) {
  margin-right: 35px;
}

@media screen and (min-width: 1280px) {
  .logo-img {
    width: 199px;
    height: 50px;
    position: absolute;
  }

  .nav-list {
    padding-left: 235px;
  }
}

.nav-list__link {
  color: #fff;
  padding-top: 15px;
  padding-bottom: 15px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.3;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.nav-list__link:hover, .nav-list__link:focus, .nav-list__link_current {
  color: var(--accent-color);
}

.mobile-menu {
  z-index: 100;
  background-color: #e17992;
  flex-direction: column;
  width: 100vw;
  max-width: 320px;
  height: 100vh;
  margin: auto;
  padding-top: 60px;
  transition: transform .35s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  inset: 0;
  transform: translateX(500%);
}

.is-open {
  transform: translateX(0);
}

@media screen and (min-width: 768px) {
  .mobile-menu {
    width: 254px;
    margin: 0;
    left: auto;
    box-shadow: -21px 0 13px #d616334f;
  }

  .is-open {
    inset: 0 0 auto auto;
  }
}

@media screen and (min-width: 1280px) {
  .mobile-menu {
    display: none;
  }
}

.mobile-menu__close-btn {
  background-color: #0000;
  border: none;
  padding: 0;
  position: absolute;
  top: 23px;
  right: 28px;
}

.mobile-menu__close-btn-icon {
  width: 14px;
  height: 14px;
}

.mobile-menu__modal-btn {
  color: #d41443;
  background-color: #fff;
  border: none;
  border-radius: 22px;
  align-self: center;
  min-width: 209px;
  height: 44px;
  margin-bottom: 32px;
  padding: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.3;
}

.mobile-menu__modal-btn-icon {
  width: 6px;
  height: 10px;
  margin-left: 12px;
}

.mobile-nav {
  background-image: repeating-linear-gradient(#fafafa66, #fafafa66 1px, #e17992 1px 42px);
  background-clip: border-box;
  padding-top: 14px;
  padding-bottom: 14px;
  padding-left: 21px;
}

.mobile-nav__item:not(:last-child) {
  margin-bottom: 22px;
}

.mobile-nav__link {
  color: #fff;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.29;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.mobile-nav__link:hover, .mobile-nav__link:focus, .mobile-nav__link_current {
  color: var(--accent-color);
}

.form-modal {
  flex-direction: column;
  margin-top: 40px;
  display: flex;
}

.form-modal__label {
  flex-direction: column;
  width: 200px;
  margin-bottom: 10px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.form-modal__input {
  letter-spacing: 0;
  text-align: left;
  color: #ffb8ca;
  font-family: DM Sans;
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
}

.form-modal_btn {
  background-color: #fff;
  border-radius: 22px;
  justify-content: center;
  align-items: center;
  width: 160px;
  height: 44px;
  margin-bottom: 10px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.form-modal_btn:hover, .form-modal_btn:focus {
  cursor: pointer;
  color: #d41443;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.hero {
  background-color: #ffb8ca;
  position: relative;
  overflow-x: hidden;
}

.hero__container {
  padding-top: 193px;
  padding-bottom: 160px;
  position: relative;
  overflow-x: visible;
}

@media screen and (min-width: 768px) {
  .hero__container {
    padding-top: 102px;
    padding-bottom: 34px;
    overflow: hidden;
  }
}

@media screen and (min-width: 1280px) {
  .hero__container {
    padding-top: 171px;
    padding-bottom: 56px;
  }
}

.hero__description-wrapper {
  width: 200px;
  position: relative;
}

@media screen and (min-width: 1280px) {
  .hero__description-wrapper {
    width: 349px;
  }
}

.hero__title {
  font-family: var(--secondary-font);
  letter-spacing: .04em;
  text-transform: uppercase;
  color: #fff;
  margin-bottom: 20px;
  font-size: 26px;
  font-weight: 400;
  line-height: 1.15;
}

@media screen and (min-width: 768px) {
  .hero__title {
    font-size: 22px;
    line-height: 1.14;
  }
}

@media screen and (min-width: 1280px) {
  .hero__title {
    margin-bottom: 25px;
    font-size: 38px;
    line-height: 1.16;
  }
}

.hero__title_accent {
  color: #d41443;
  font-size: 48px;
  line-height: .96;
}

@media screen and (min-width: 768px) {
  .hero__title_accent {
    font-size: 34px;
    line-height: .9;
  }
}

@media screen and (min-width: 1280px) {
  .hero__title_accent {
    font-size: 58px;
  }
}

.hero__btn-list {
  align-items: center;
  display: flex;
}

.hero__btn-list-item:not(:last-child) {
  margin-right: 8px;
}

@media screen and (min-width: 1280px) {
  .hero__btn-list-item:not(:last-child) {
    margin-right: 15px;
  }
}

.hero__btn-list-link {
  color: #d41443;
  background-color: #fff;
  border-radius: 20px;
  padding: 8px 14px;
  font-size: 8px;
  line-height: 1.25;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-flex;
}

.hero__btn-list-link:hover, .hero__btn-list-link:focus {
  background-color: #ecebeb;
}

@media screen and (min-width: 768px) {
  .hero__btn-list-link {
    padding: 9px 14px;
  }
}

@media screen and (min-width: 1280px) {
  .hero__btn-list-link {
    padding: 14px 24px;
    font-size: 14px;
    line-height: 1.3;
  }
}

.hero__btn-list-link_accent {
  background-color: var(--accent-color);
  color: #fff;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

.hero__btn-list-link_accent:hover, .hero__btn-list-link_accent:focus {
  background-color: #c7133e;
}

.hero__img-center {
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  animation-name: appearance;
  animation-duration: 4s;
  animation-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 94px;
  right: 60px;
}

.hero__img-center:hover, .hero__img-center:focus {
  transform: scale(1.1);
}

@media screen and (min-width: 768px) {
  .hero__img-center {
    top: 82px;
    right: 300px;
  }
}

@media screen and (min-width: 1280px) {
  .hero__img-center {
    top: 108px;
    right: 650px;
  }
}

.hero__ellipse {
  background-color: #ffa5ba;
  border-radius: 50%;
  width: 346px;
  height: 344px;
  animation-name: scale;
  animation-duration: 2s;
  animation-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 83px;
  left: 36px;
}

@media screen and (min-width: 768px) {
  .hero__ellipse {
    width: 324px;
    height: 322px;
    top: 31px;
    left: 303px;
  }
}

@media screen and (min-width: 1280px) {
  .hero__ellipse {
    width: 538px;
    height: 538px;
    top: 52px;
    left: 485px;
  }
}

.hero__img-left {
  animation-name: appearance;
  animation-duration: 4s;
  animation-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  bottom: 28px;
  left: 0;
}

@media screen and (min-width: 1280px) {
  .hero__img-left {
    left: 77px;
  }
}

.hero__text {
  color: #fff;
  margin-top: 66px;
  margin-bottom: 7px;
  font-size: 8px;
  line-height: 1.5;
}

@media screen and (min-width: 1280px) {
  .hero__text {
    margin-top: 120px;
    font-size: 11px;
    line-height: 1.4;
  }
}

@media screen and (min-width: 768px) {
  .hero__description-wrapper {
    flex-direction: column;
    display: flex;
  }
}

.hero__text-wrapper {
  align-self: flex-end;
  width: 114px;
}

@media screen and (min-width: 1280px) {
  .hero__text-wrapper {
    justify-content: space-around;
    align-items: center;
    width: 200px;
    margin-left: 56px;
    display: flex;
  }
}

.hero__btn {
  cursor: pointer;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 6px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.hero__btn:hover, .hero__btn:focus {
  background-color: var(--accent-color);
  fill: #fff;
}

.hero__btn:hover .hero__btn-icon, .hero__btn:focus .hero__btn-icon {
  fill: #fff;
}

@media screen and (min-width: 1280px) {
  .hero__btn {
    width: 26px;
    height: 26px;
    padding: 8px;
    position: absolute;
    top: 500px;
    left: 310px;
  }
}

.hero__btn-icon {
  fill: var(--accent-color);
  width: 8px;
  height: 8px;
}

.hero__stat-list {
  position: absolute;
  top: 96px;
  right: 44px;
}

@media screen and (min-width: 1280px) {
  .hero__stat-list {
    margin-right: 56px;
    top: 156px;
    right: 115px;
  }
}

.hero__stat-list-item {
  position: relative;
}

.hero__stat-list-item:not(:last-child) {
  margin-bottom: 13px;
}

.hero__stat-list-item:before {
  content: "";
  z-index: 0;
  background-color: #fff;
  border-radius: 50%;
  width: 16px;
  height: 16px;
  display: block;
  position: absolute;
  top: 0;
  right: -10px;
}

@media screen and (min-width: 1280px) {
  .hero__stat-list-item:before {
    width: 30px;
    height: 30px;
  }
}

.hero__stat-list-accent-text {
  font-family: var(--secondary-font);
  letter-spacing: .04em;
  text-transform: uppercase;
  text-align: right;
  color: #d41443;
  z-index: 2;
  font-size: 28px;
  font-weight: 400;
  line-height: 1.14;
  position: relative;
}

.hero__stat-list-text {
  text-align: right;
  color: #fff;
  font-size: 8px;
  line-height: 1.25;
}

@media screen and (min-width: 1280px) {
  .hero__stat-list-accent-text {
    font-size: 46px;
  }

  .hero__stat-list-text {
    font-size: 14px;
    line-height: 1.29;
  }
}

.hero__img-right {
  animation-name: appearance;
  animation-duration: 6s;
  animation-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  bottom: 0;
  right: 34px;
}

@media screen and (min-width: 1280px) {
  .hero__img-right {
    right: 109px;
  }
}

@media screen and (max-width: 767px) {
  .hero__text-wrapper, .hero__stat-list, .hero__img-left, .hero__img-right {
    display: none;
  }
}

@keyframes appearance {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes scale {
  0% {
    transform: scale(.25);
  }

  100% {
    transform: scale(1);
  }
}

.backdrop-buynow {
  z-index: 300;
  background-color: #0000000f;
  width: 100vw;
  height: 100vh;
  transition: all .25s linear;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop-buynow .modal {
  background-color: #fff5f6;
  border: 2px solid #d41443;
  border-radius: 4px;
  width: 450px;
  height: 570px;
  padding: 80px;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow-x: hidden;
  overflow-y: scroll;
  transform: translate(-50%, -50%);
}

.backdrop-buynow .modal__close-btn {
  cursor: pointer;
  fill: var(--accent-color);
  background: none;
  border: 1px solid #ee0909c2;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 27px;
  height: 27px;
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.backdrop-buynow .modal__close-btn:hover {
  fill: #fff;
  background-color: #d41443;
  border: 1px solid #d41443;
}

.backdrop-buynow.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.backdrop-buynow .modal-form {
  flex-direction: column;
  display: flex;
}

.backdrop-buynow .modal-form__title {
  font-family: var(--primary-font);
  text-align: center;
  letter-spacing: .03em;
  color: #d41443;
  margin-bottom: 12px;
  font-size: 20px;
  font-style: normal;
  font-weight: bold;
  line-height: 23px;
}

.backdrop-buynow .modal-form__lable-name {
  font-family: var(--primary-font);
  letter-spacing: .01em;
  color: #d41443;
  font-size: 14px;
  font-style: normal;
  font-weight: bold;
  line-height: 1.16;
}

.backdrop-buynow .modal-form__span {
  margin-top: 4px;
  margin-bottom: 10px;
  display: block;
  position: relative;
}

.backdrop-buynow .modal-form__input {
  cursor: pointer;
  border: 1px solid #d35a5a33;
  border-radius: 4px;
  width: 100%;
  height: 40px;
  padding-left: 42px;
  transition: border-color .25s cubic-bezier(.4, 0, .2, 1);
}

.backdrop-buynow .modal-form__input:focus {
  border-color: #dd230f;
  outline: none;
}

.backdrop-buynow .modal-form__message {
  resize: none;
  color: #212121;
  cursor: pointer;
  border: none;
  border-radius: 4px;
  width: 100%;
  height: 120px;
  margin-top: 4px;
  padding: 12px 16px;
  transition: border-color .25s cubic-bezier(.4, 0, .2, 1);
}

.backdrop-buynow .modal-form__message:focus {
  border: 1px solid #dd230f;
  outline: none;
}

.backdrop-buynow .modal-form__message:focus::placeholder {
  font-family: var(--primary-font);
  letter-spacing: .01em;
  color: #75757580;
  font-size: 14px;
  font-style: normal;
  font-weight: normal;
  line-height: 14px;
}

.backdrop-buynow .modal-form__ckbox-description {
  font-family: var(--primary-font);
  letter-spacing: .03em;
  color: #dd230f;
  justify-content: center;
  margin-top: 5px;
  margin-bottom: 10px;
  font-size: 14px;
  font-style: normal;
  font-weight: normal;
  line-height: 1.2;
}

.backdrop-buynow .modal-form__contract-description {
  font-family: var(--primary-font);
  letter-spacing: .03em;
  color: var(--accent-text-color);
  font-size: 14px;
  font-style: normal;
  font-weight: normal;
  line-height: 1.16;
  text-decoration-line: underline;
}

.products {
  text-align: center;
  text-transform: uppercase;
  background-color: #fff;
  justify-content: center;
  margin-top: 60px;
  font-family: Titan One, cursive;
  font-size: 18px;
  line-height: 21px;
  display: flex;
}

.products__container {
  width: 320px;
}

.products__title {
  color: #ffa5ba;
  margin-top: 60px;
}

.products__title--emphasized {
  color: #d41443;
  text-align: center;
  letter-spacing: .04em;
  text-transform: uppercase;
  margin-top: 16px;
  font-size: 34px;
  font-weight: 400;
  line-height: 39px;
}

.products-list__item {
  border-radius: 24px;
  flex-direction: column;
  align-items: center;
  width: 280px;
  margin-top: 140px;
  margin-left: 20px;
  padding-top: 160px;
  padding-bottom: 40px;
  list-style-type: none;
  transition-property: transform, box-shadow;
  display: flex;
  position: relative;
}

.products-list__item--ice-cream {
  background-color: #ffb8ca;
}

.products-list__item--ice-coffee {
  background-color: #f0d1a5;
}

.products-list__item--milkshakes {
  background-color: #c2e297;
  margin-bottom: 60px;
}

.products-list__item:focus, .products-list__item:hover {
  transition-property: transform, box-shadow;
